@media screen and ( max-width: 1200px ) {
    html {
        font-size: 12px; }
    .nav {
        &__item {
            margin-right: 1rem; } } }
@media screen and ( min-width: 992px ) {
    .logo {
        &:hover {
            color: $light; } }
    .phone {
        &:hover {
            color: $light; } }
    .button {
        &:hover {
            box-shadow: none;
            border: 1px solid #fff;
            box-shadow: 0px 5px 15px rgba($light, 0.3);
            background-color: rgba($light, 0.3); }
        &_outline {
            &:hover {
                box-shadow: 0px 5px 15px rgba($light, 0.3);
                background-color: rgba($light, 0.3); } } }
    .control {
        &__button {
            &:hover {
                box-shadow: none;
                border: 1px solid $dark;
                background-color: $dark; } } }
    .hero {
        &__scroll-down {
            &:hover {
                color: $primary;
                box-shadow: 0px 0px 10px rgba($light, 0.3); } } }
    .types {
        &__title {
            font-size: 1.245rem; } }
    .footer {
        &__link {
            &:hover {
                background: none; } }
        &__number {
            &:hover {
                color: $light; } } } }
@media screen and ( max-width: 991px ) {
