            width: 100%;
            max-width: 360px; }
        &>.container {
            display: flex;
            flex-direction: column; }
        &__map {
            order: 1;
            margin-bottom: 3.3rem; }
        &__address {
            font-size: 18px;
            line-height: 25px;
            max-width: 320px;
            margin: 0 auto 21px;
