                width: 20px; } } }
    .policy {
        &__label {
            font-size: 1.343rem;
            &_center {
                justify-content: flex-start; } } } }
@media screen and ( max-width: 760px ) {
    .row {
        flex-direction: column;
        align-items: center; }
    .types {
        &__card {
            margin-bottom: 5rem; }
        &__text {
            margin-left: 0; } }
    .design {
        background-color: $dark;
        color: $light;
        &__col {
            margin-bottom: 5.8rem; }
        &__text {
            margin-left: 0; } } }
