.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
  background: $dark url('../img/hero-bg.jpg') no-repeat center / cover;
  height: 100vh;
  color: $light;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($dark, 0.7); }
  &__content {
    max-width: 47.85rem;
    position: relative;
    margin-left: 2.48rem; }
  &__title {
    margin-bottom: 1rem; }
  &__text {
    line-height: 131%;
    font-size: 1.57rem;
    max-width: 500px;
    margin: 0;
    margin-bottom: 3.6rem;
    padding-top: 1.1rem; }
  &__button {
    margin-right: 17px;
    padding: 18px 25px;
    min-width: 206px; }
  &__instagram {
    font-family: 'Futura Demi';
    position: absolute;
    left: 0;
    top: 0;
    max-width: 80px;
    height: 100%; }
  &__scroll-down {
    font-family: 'Yeseva One', cursive;
    position: absolute;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    bottom: 0.3rem;
    min-width: 17.6rem;
    min-height: 6.92rem;
    background-color: $dark;
    text-transform: uppercase;
    text-decoration: none;
    color: $light;
    font-size: 0.858rem;
    &.active {
      animation-name: slideUp;
      animation-duration: 0.5s;
      animation-timing-function: ease-in;
      animation-delay: 3s;
      animation-fill-mode: forwards;
      opacity: 0; }
    &::after {
      content: '';
      background: url('../img/arrow-down.svg') no-repeat center;
      width: 30px;
      height: 30px;
      border: 2px solid $primary;
      border-radius: 50%;
      margin-left: 17px; } } }
.instagram {
  background-color: $dark;
  justify-content: center;
  align-items: center;
  display: flex;
  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    color: $primary;
    transform: rotate(90deg);
    line-height: 0.8;
    font-size: 1rem;
    &::after {
      content: '';
      width: 50px;
      height: 1px;
      background-color: $primary;
      margin-left: 9px; }
    &:hover {
      color: $light; }
    &:hover:after {
      background-color: $light; } } }
.title {
  margin: 0;
  font-family: 'Yeseva One', cursive;
  line-height: 1.4;
  text-transform: uppercase;
  font-size: 2.85rem;
  margin-top: -6rem;
  font-weight: 400;
  &_primary {
    color: $primary; } }

